$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

// Mixins
@mixin transition($transitions) {
     -moz-transition: $transitions;
       -o-transition: $transitions;
      -ms-transition: $transitions;
  -webkit-transition: $transitions;
          transition: $transitions;
}

.page-template-page-apply {
  #applyIntro {
    .applyStepContainer {
      h3 {
        font-size: 1.25rem;
        margin-top: 20px;
      }
    }
  }
  #applyForm {
    background: #f2f2f2;
    .applyFormContainer {
      background: $white;
      padding: 4rem;
      border-top: 2px solid $yellow;
      margin-top: 40px;
      box-shadow: 0 0 25px -9px #333;
      transform: scale(1.01);
      hr {
        width: 100px;
        border-color: $yellow;
        margin-left: 0;
        border-width: 2px;
      }
      input {
        width: 100%;
        padding: 10px;
        border-color: #f2f2f2;
        border-radius: 0;
        margin: 5px;
      }
      &.inactive {
        transform: scale(1);
        box-shadow: none;
        border-top-color: #ddd;
        hr {
          border-color: #ddd;
        }
      }
    }
  }
  #applySeeMore {
    background-color: $blue;
    padding-top: 6rem;
    padding-bottom: 6rem;
    color: #fff;
  }
}